body {
  margin: 0; 
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(#7b7b7b, #5ccab5);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main{
  height: 90vh;
  width: 80%;
  margin: 0 auto;
}
.form-container{
  background-color: #2c5046;
  height: 70vh;
  padding: 100px 50px 0 50px ;
  margin: auto;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #fff;
  width: 100%;
  display: block;
  padding-bottom: 4px;
  letter-spacing: 2px; 
  font-weight: lighter;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
  text-transform: capitalize;
}
button{
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #000;
  color: #fff;
  font-weight: 100;
}

button:active{
  outline: none;
}
.info{
  width: 100%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}
.info_key{
  color: #fff;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  font-weight: 400;
}
.info_key:last-child{
  border: 0;
}
.info_value{
  color: #fff;
  font-weight: 200;
}







 /* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .form-container{
    background-color: #2c5046;
    height: 100vh;
    padding: 70px 50px 0 50px ;
    margin: auto;
  }
}









